// components/PricingConfigManager.jsx
"use client";

import { useState, useEffect } from "react";
import { Save, RefreshCw, Lock, Unlock, AlertCircle } from "lucide-react";

export default function PricingConfigManager() {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [password, setPassword] = useState("");
  const [config, setConfig] = useState(null);
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [deploying, setDeploying] = useState(false);
  const [message, setMessage] = useState("");

  const ADMIN_PASSWORD = "admin123"; // Change this!

  useEffect(() => {
    loadConfig();
  }, []);

  const loadConfig = async () => {
    try {
      const result = await window.storage.get("pricing_config");
      if (result) {
        setConfig(JSON.parse(result.value));
      } else {
        setConfig(getDefaultConfig());
      }
    } catch (error) {
      console.error("Error loading config:", error);
      setConfig(getDefaultConfig());
    } finally {
      setLoading(false);
    }
  };

  const getDefaultConfig = () => ({
    diamondMargins: {
      lessThan1ct: {
        multiplier: 2.2,
        flatAdd: 900,
        description: "Diamonds less than 1 carat",
      },
      greaterOrEqual1ct: {
        multiplier: 2.7,
        flatAdd: 0,
        description: "Diamonds 1 carat or more",
      },
      flatFees: {
        fee1: 150,
        fee2: 700,
        description: "Flat fees added to all diamond calculations",
      },
    },
    makingCharges: {
      lightWeight: {
        threshold: 2,
        rate: 950,
        description: "Gold weight less than 2g",
      },
      heavyWeight: {
        rate: 700,
        description: "Gold weight 2g or more",
      },
      multiplier: 1.75,
      description: "Final making charge multiplier",
    },
    gstRate: 0.03,
  });

  const saveConfig = async () => {
    try {
      setSaving(true);
      await window.storage.set("pricing_config", JSON.stringify(config));
      setMessage(
        '‚úÖ Configuration saved! Click "Deploy Changes" to update all product pages.'
      );
      setTimeout(() => setMessage(""), 5000);
    } catch (error) {
      setMessage("‚ùå Error saving configuration");
      console.error("Error saving config:", error);
    } finally {
      setSaving(false);
    }
  };

  const deployChanges = async () => {
    try {
      setDeploying(true);

      const revalidateResponse = await fetch("/api/revalidate", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ password: ADMIN_PASSWORD }),
      });

      const revalidateData = await revalidateResponse.json();

      if (revalidateResponse.ok) {
        setMessage(
          "üöÄ All product pages updated successfully! New prices are live."
        );
      } else {
        setMessage(
          "‚ö†Ô∏è Cache refresh failed. Changes will apply in 1 hour or try again."
        );
      }

      setTimeout(() => setMessage(""), 5000);
    } catch (error) {
      setMessage("‚ùå Error deploying changes");
      console.error("Error deploying:", error);
    } finally {
      setDeploying(false);
    }
  };

  const handleLogin = (e) => {
    e.preventDefault();
    if (password === ADMIN_PASSWORD) {
      setIsAuthenticated(true);
      setPassword("");
    } else {
      setMessage("‚ùå Incorrect password");
      setTimeout(() => setMessage(""), 3000);
    }
  };

  const updateConfig = (path, value) => {
    const newConfig = { ...config };
    const keys = path.split(".");
    let current = newConfig;

    for (let i = 0; i < keys.length - 1; i++) {
      current = current[keys[i]];
    }

    current[keys[keys.length - 1]] = parseFloat(value) || 0;
    setConfig(newConfig);
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <RefreshCw className="w-8 h-8 animate-spin mx-auto mb-4 text-blue-600" />
          <p className="text-gray-600">Loading configuration...</p>
        </div>
      </div>
    );
  }

  if (!isAuthenticated) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100 flex items-center justify-center p-4">
        <div className="bg-white rounded-2xl shadow-xl p-8 max-w-md w-full">
          <div className="text-center mb-8">
            <Lock className="w-16 h-16 mx-auto mb-4 text-indigo-600" />
            <h1 className="text-3xl font-bold text-gray-800 mb-2">
              Admin Access
            </h1>
            <p className="text-gray-600">
              Enter password to manage pricing configuration
            </p>
          </div>

          <form onSubmit={handleLogin} className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Password
              </label>
              <input
                type="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-transparent"
                placeholder="Enter admin password"
              />
            </div>

            <button
              type="submit"
              className="w-full bg-indigo-600 text-white py-3 rounded-lg font-medium hover:bg-indigo-700 transition-colors"
            >
              Login
            </button>
          </form>

          {message && (
            <div className="mt-4 p-3 bg-red-50 text-red-600 rounded-lg text-center text-sm">
              {message}
            </div>
          )}
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 p-4 md:p-8">
      <div className="max-w-6xl mx-auto">
        {/* Header */}
        <div className="bg-white rounded-2xl shadow-sm p-6 mb-6">
          <div className="flex items-center justify-between flex-wrap gap-4">
            <div className="flex items-center gap-3">
              <Unlock className="w-8 h-8 text-green-600" />
              <div>
                <h1 className="text-3xl font-bold text-gray-800">
                  Pricing Configuration
                </h1>
                <p className="text-gray-600">
                  Manage margins and pricing parameters
                </p>
              </div>
            </div>
            <button
              onClick={() => setIsAuthenticated(false)}
              className="px-4 py-2 text-sm text-gray-600 hover:text-gray-800 border border-gray-300 rounded-lg hover:bg-gray-50"
            >
              Logout
            </button>
          </div>
        </div>

        {message && (
          <div className="mb-6 p-4 bg-green-50 text-green-700 rounded-lg text-center font-medium flex items-center justify-center gap-2">
            <AlertCircle className="w-5 h-5" />
            {message}
          </div>
        )}

        {/* Current Formula Display */}
        <div className="bg-gradient-to-br from-blue-600 to-indigo-600 rounded-2xl shadow-sm p-6 mb-6 text-white">
          <h2 className="text-2xl font-bold mb-4">üìê Current Formula</h2>
          <div className="space-y-2 font-mono text-sm">
            <p>
              <strong>Diamonds &lt; 1ct:</strong> Base √ó{" "}
              {config.diamondMargins.lessThan1ct.multiplier} + ‚Çπ
              {config.diamondMargins.lessThan1ct.flatAdd}
            </p>
            <p>
              <strong>Diamonds ‚â• 1ct:</strong> Base √ó{" "}
              {config.diamondMargins.greaterOrEqual1ct.multiplier}
            </p>
            <p>
              <strong>Making (light):</strong> Weight √ó ‚Çπ
              {config.makingCharges.lightWeight.rate} √ó{" "}
              {config.makingCharges.multiplier}
            </p>
            <p>
              <strong>Making (heavy):</strong> Weight √ó ‚Çπ
              {config.makingCharges.heavyWeight.rate} √ó{" "}
              {config.makingCharges.multiplier}
            </p>
            <p>
              <strong>GST:</strong> {(config.gstRate * 100).toFixed(1)}%
            </p>
          </div>
        </div>

        {/* Rest of the form... */}
        {/* Diamond Margins */}
        <div className="bg-white rounded-2xl shadow-sm p-6 mb-6">
          <h2 className="text-2xl font-bold text-gray-800 mb-6">
            üíé Diamond Pricing
          </h2>

          <div className="mb-6 p-4 bg-blue-50 rounded-xl">
            <h3 className="font-semibold text-lg mb-3 text-blue-900">
              {config.diamondMargins.lessThan1ct.description}
            </h3>
            <div className="grid md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Multiplier
                </label>
                <input
                  type="number"
                  step="0.1"
                  value={config.diamondMargins.lessThan1ct.multiplier}
                  onChange={(e) =>
                    updateConfig(
                      "diamondMargins.lessThan1ct.multiplier",
                      e.target.value
                    )
                  }
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Flat Addition (‚Çπ)
                </label>
                <input
                  type="number"
                  value={config.diamondMargins.lessThan1ct.flatAdd}
                  onChange={(e) =>
                    updateConfig(
                      "diamondMargins.lessThan1ct.flatAdd",
                      e.target.value
                    )
                  }
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                />
              </div>
            </div>
          </div>

          <div className="mb-6 p-4 bg-purple-50 rounded-xl">
            <h3 className="font-semibold text-lg mb-3 text-purple-900">
              {config.diamondMargins.greaterOrEqual1ct.description}
            </h3>
            <div className="grid md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Multiplier
                </label>
                <input
                  type="number"
                  step="0.1"
                  value={config.diamondMargins.greaterOrEqual1ct.multiplier}
                  onChange={(e) =>
                    updateConfig(
                      "diamondMargins.greaterOrEqual1ct.multiplier",
                      e.target.value
                    )
                  }
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Flat Addition (‚Çπ)
                </label>
                <input
                  type="number"
                  value={config.diamondMargins.greaterOrEqual1ct.flatAdd}
                  onChange={(e) =>
                    updateConfig(
                      "diamondMargins.greaterOrEqual1ct.flatAdd",
                      e.target.value
                    )
                  }
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500"
                />
              </div>
            </div>
          </div>

          <div className="p-4 bg-green-50 rounded-xl">
            <h3 className="font-semibold text-lg mb-3 text-green-900">
              {config.diamondMargins.flatFees.description}
            </h3>
            <div className="grid md:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Flat Fee 1 (‚Çπ)
                </label>
                <input
                  type="number"
                  value={config.diamondMargins.flatFees.fee1}
                  onChange={(e) =>
                    updateConfig("diamondMargins.flatFees.fee1", e.target.value)
                  }
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Flat Fee 2 (‚Çπ)
                </label>
                <input
                  type="number"
                  value={config.diamondMargins.flatFees.fee2}
                  onChange={(e) =>
                    updateConfig("diamondMargins.flatFees.fee2", e.target.value)
                  }
                  className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-green-500"
                />
              </div>
            </div>
          </div>
        </div>

        {/* Making Charges */}
        <div className="bg-white rounded-2xl shadow-sm p-6 mb-6">
          <h2 className="text-2xl font-bold text-gray-800 mb-6">
            üî® Making Charges
          </h2>

          <div className="grid md:grid-cols-2 gap-4 mb-4">
            <div className="p-4 bg-orange-50 rounded-xl">
              <h3 className="font-semibold mb-3 text-orange-900">
                {config.makingCharges.lightWeight.description}
              </h3>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Rate per gram (‚Çπ)
              </label>
              <input
                type="number"
                value={config.makingCharges.lightWeight.rate}
                onChange={(e) =>
                  updateConfig("makingCharges.lightWeight.rate", e.target.value)
                }
                className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-orange-500"
              />
            </div>

            <div className="p-4 bg-amber-50 rounded-xl">
              <h3 className="font-semibold mb-3 text-amber-900">
                {config.makingCharges.heavyWeight.description}
              </h3>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Rate per gram (‚Çπ)
              </label>
              <input
                type="number"
                value={config.makingCharges.heavyWeight.rate}
                onChange={(e) =>
                  updateConfig("makingCharges.heavyWeight.rate", e.target.value)
                }
                className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-amber-500"
              />
            </div>
          </div>

          <div className="p-4 bg-red-50 rounded-xl">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Making Charge Multiplier
            </label>
            <input
              type="number"
              step="0.01"
              value={config.makingCharges.multiplier}
              onChange={(e) =>
                updateConfig("makingCharges.multiplier", e.target.value)
              }
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-red-500"
            />
            <p className="text-xs text-gray-500 mt-1">
              {config.makingCharges.description}
            </p>
          </div>
        </div>

        {/* GST */}
        <div className="bg-white rounded-2xl shadow-sm p-6 mb-6">
          <h2 className="text-2xl font-bold text-gray-800 mb-6">üìä GST Rate</h2>
          <div className="p-4 bg-indigo-50 rounded-xl">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              GST Percentage ({(config.gstRate * 100).toFixed(1)}%)
            </label>
            <input
              type="number"
              step="0.01"
              value={config.gstRate}
              onChange={(e) => updateConfig("gstRate", e.target.value)}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500"
            />
            <p className="text-xs text-gray-500 mt-1">
              Enter as decimal (e.g., 0.03 for 3%)
            </p>
          </div>
        </div>

        {/* Save & Deploy Buttons */}
        <div className="bg-white rounded-2xl shadow-sm p-6 space-y-4">
          <button
            onClick={saveConfig}
            disabled={saving}
            className="w-full bg-gradient-to-r from-blue-600 to-indigo-600 text-white py-4 rounded-xl font-semibold text-lg hover:from-blue-700 hover:to-indigo-700 transition-all disabled:opacity-50 flex items-center justify-center gap-2"
          >
            {saving ? (
              <>
                <RefreshCw className="w-5 h-5 animate-spin" />
                Saving...
              </>
            ) : (
              <>
                <Save className="w-5 h-5" />
                Save Configuration
              </>
            )}
          </button>

          <button
            onClick={deployChanges}
            disabled={deploying}
            className="w-full bg-gradient-to-r from-green-600 to-emerald-600 text-white py-4 rounded-xl font-semibold text-lg hover:from-green-700 hover:to-emerald-700 transition-all disabled:opacity-50 flex items-center justify-center gap-2"
          >
            {deploying ? (
              <>
                <RefreshCw className="w-5 h-5 animate-spin" />
                Deploying...
              </>
            ) : (
              <>
                <AlertCircle className="w-5 h-5" />
                Deploy Changes Now
              </>
            )}
          </button>

          <p className="text-xs text-gray-500 text-center">
            Save changes first, then click "Deploy" to update all product pages
            immediately.
            <br />
            Without deploying, changes will apply automatically in 1 hour.
          </p>
        </div>
      </div>
    </div>
  );
}
